"use client";

import ScrollAnimation from '../components/ScrollAnimation';
import { useWorkPage } from '../hooks/useWorkPage';

export default function WorksPage() {
  const { data, loading, error } = useWorkPage();

  // Convert data to array and sort by most recent
  const workExperiences = data
    ? Object.entries(data)
        .map(([id, work]) => ({ id, ...work }))
        .sort((a, b) => {
          // Prioritize current positions (end_date = "present") first
          if (
            a.end_date.toLowerCase() === 'present' &&
            b.end_date.toLowerCase() !== 'present'
          ) {
            return -1;
          }
          if (
            b.end_date.toLowerCase() === 'present' &&
            a.end_date.toLowerCase() !== 'present'
          ) {
            return 1;
          }
          // If both are current or both are past positions, sort by start_date
          const parseDate = (dateString: string) => {
            const [month, year] = dateString.split(' ');
            const monthMap: { [key: string]: number } = {
              Jan: 0, Feb: 1, Mar: 2, Apr: 3, May: 4, Jun: 5,
              Jul: 6, Aug: 7, Sep: 8, Oct: 9, Nov: 10, Dec: 11,
            };
            return new Date(parseInt(year), monthMap[month] || 0);
          };
          const dateA = parseDate(a.start_date);
          const dateB = parseDate(b.start_date);
          return dateB.getTime() - dateA.getTime();
        })
    : [];

  // Responsive grid columns
  const gridCols =
    'grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8 md:gap-10 lg:gap-12';

  // Color schemes for cards
  const colorSchemes = [
    'bg-teal-100 border-teal-300',
    'bg-red-100 border-red-300',
    'bg-yellow-100 border-yellow-300',
    'bg-blue-100 border-blue-300',
    'bg-purple-100 border-purple-300',
    'bg-orange-100 border-orange-300',
  ];

  return (
    <div className="min-h-screen w-full bg-[#F5F5F5] py-16 md:py-24 lg:py-32 px-4 md:px-12 lg:px-32 xl:px-56">
      {/* Header */}
      <div className="text-center mb-12 md:mb-20">
        <h1 className="text-[#0A2F3B] text-[32px] md:text-[60px] lg:text-[80px] font-bold mb-4 md:mb-6">
          All Work Experiences
        </h1>
        <p className="text-gray-600 text-[16px] md:text-[20px] lg:text-[24px] max-w-3xl mx-auto">
          Explore my professional journey, roles, and key contributions in detail.
        </p>
      </div>

      {/* Loading State */}
      {loading && (
        <div className="flex flex-col items-center justify-center min-h-[300px]">
          <div className="animate-spin rounded-full h-20 w-20 border-b-2 border-[#286F6E] mb-4"></div>
          <p className="text-lg text-gray-600">Loading work experiences...</p>
        </div>
      )}

      {/* Error State */}
      {error && (
        <div className="flex flex-col items-center justify-center min-h-[300px]">
          <p className="text-lg text-red-500">Error loading work experiences: {error}</p>
        </div>
      )}

      {/* Empty State */}
      {!loading && !error && workExperiences.length === 0 && (
        <div className="flex flex-col items-center justify-center min-h-[300px]">
          <p className="text-lg text-gray-500">No work experiences found.</p>
        </div>
      )}

      {/* Grid of Work Experiences */}
      {!loading && !error && workExperiences.length > 0 && (
        <div className={gridCols}>
          {workExperiences.map((work, idx) => (
            <ScrollAnimation key={work.id} delay={idx * 0.1}>
              <div
                className={`relative group rounded-3xl border-2 shadow-lg p-8 md:p-10 transition-all duration-300 cursor-pointer hover:scale-[1.03] hover:shadow-2xl ${
                  colorSchemes[idx % colorSchemes.length]
                }`}
                tabIndex={0}
                aria-label={`Work at ${work.company}: ${work.title}`}
              >
                {/* Company & Title */}
                <div className="mb-4">
                  <h2 className="text-2xl md:text-3xl font-bold text-[#286F6E] group-hover:underline">
                    {work.company}
                  </h2>
                  <p className="text-lg md:text-xl text-gray-700 mt-1 font-medium">
                    {work.title}
                  </p>
                </div>
                {/* Dates */}
                <div className="flex items-center gap-2 mb-4">
                  <span className="inline-block px-3 py-1 rounded-full bg-white text-[#286F6E] text-xs font-semibold border border-[#286F6E]">
                    {work.start_date} - {work.end_date}
                  </span>
                </div>
                {/* Description */}
                <p className="text-gray-600 text-base md:text-lg mb-4 line-clamp-4 group-hover:line-clamp-none transition-all duration-300">
                  {work.description}
                </p>
                {/* Interactive More Info (expand on hover/focus) */}
                <div className="absolute bottom-6 right-8 opacity-0 group-hover:opacity-100 group-focus:opacity-100 transition-opacity duration-300">
                  <span className="text-[#286F6E] font-semibold text-sm bg-white px-4 py-2 rounded-full shadow-md border border-[#286F6E]">
                    See Details
                  </span>
                </div>
              </div>
            </ScrollAnimation>
          ))}
        </div>
      )}
    </div>
  );
} 